import {ComponentProps} from 'react';
import clsx from 'clsx';

interface Props extends ComponentProps<'div'> {}

export function Skeleton({className, ...props}: Props) {
  return (
    <div className={clsx('relative', className)} {...props}>
      <div className="absolute inset-0 bg-gradient-to-r from-gray-50 via-gray-200 to-gray-50" />
      <div className="absolute inset-0 bg-gradient-to-r from-gray-200 via-gray-50 to-gray-200 animate-shimmer" />
    </div>
  );
}
